.hero {
    padding-top: 105px;
    padding-bottom: 105px;

    &__content-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__content {
        max-width: 545px;
    }

    &__sub-title-wrap {
        color: #141313;
        font-size: 32px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: -0.96px;
        margin-bottom: 48px;
    }

    &__title {
        margin-bottom: 24px;
    }

    &__sub-title,
    &__pro-status {
        display: inline-block;
        margin-right: 10px;
    }

    &__pro-status,
    &__location {
        font-style: italic;
        font-weight: 700;
    }

    &__sub-title {
    }

    &__pro-status {
    }

    &__location {

    }

    &__contact-wrap {
        display: flex;
        flex-wrap: wrap;
    }

    &__phone {
        margin-right: 48px;

        .hero__contact-icon {
            &:before {
                background: url(data:image/svg+xml;base64,) center center no-repeat;
                background-size: contain;
            }
        }

        &:hover {
            .hero__contact-icon {
                &:before {
                    background: url(data:image/svg+xml;base64,) center center no-repeat;
                }
            }
        }
    }

    &__email {

        .hero__contact-icon {
            &:before {
                background: url(data:image/svg+xml;base64,) center center no-repeat;
                background-size: contain;
            }
        }

        &:hover {
            .hero__contact-icon {
                &:before {
                    background: url(data:image/svg+xml;base64,) center center no-repeat;
                }
            }
        }
    }

    &__contact-item {
        font-size: 18px;
        font-style: italic;
        font-weight: 700;
        line-height: 1.33;
        position: relative;
        z-index: 1;
        display: flex;
        align-items: center;

        &:hover {

            .hero__contact-icon {
                background: var(--wp--preset--gradient--orange-to-lilac);
            }
        }
    }

    &__contact-icon {
        display: inline-block;
        width: 32px;
        height: 32px;
        margin-right: 12px;
        background: rgba(20, 19, 19, 0.05);
        position: relative;
        z-index: 1;
        border-radius: 50%;
        overflow: hidden;

        &:before {
            position: absolute;
            content: '';
            display: block;
            left: 50%;
            top: 50%;
            width: 21px;
            height: 21px;
            transform: translate(-50%, -50%);
            z-index: 1;
        }

    }

    &__btn {
        margin-bottom: 64px;
    }

    &__image {

    }

    &__text {

    }

    &__user-wrap {
        position: relative;
        z-index: 1;


        &:before,
        &:after {
            position: absolute;
            content: '';
            display: block;
            z-index: 1;
        }

        &:before {
            width: 658px;
            height: 713px;
            z-index: -1;
            left: -80px;
            top: 34px;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
            background-size: contain;
        }

        &:after {
            width: 619px;
            height: 719px;
            z-index: 2;
            left: -40px;
            top: 30px;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
            background-size: contain;
        }
    }
}
